.back {
	font-size: 80%;
	position: relative;
	line-height: 1;
	margin-left: 1.2em;
	&:before {
		font-family: 'FontAwesome';
		content: "\f060";
		margin-right: .25em;
		position: absolute;
		left: -1.2em;
		top: .1em;
	}
}

.single-caso {
	.content-area {
		
	}
	
	.entry-title {
		margin: 1em 0 .25em;
	}
	.case-content {
		
		.case__wrap {
			margin: 0;
					
			@media screen and (min-width: $break__medium) {
				margin: 0;
				display: flex;
				justify-content: space-between;
				align-items: flex-start;
				padding: 1em 0;
				border-top: 3px solid $color__interactive;
				border-bottom: 3px solid $color__interactive;
			}
			
			
			
			.case-desc {
				width: 100%;
				margin: 0 auto;
				@media screen and (min-width: $break__medium) {
					padding: 0 1em;
					width: 49%;
					height: 100%;
				}
			}
			.case-trends {
				display: block;
				padding: .5em 1em;
				margin: .5em 0;
				border: none;
			}
			//more info
			.enlace {
				margin: .5em 0;
				display: block;
				padding: .5em 1em;
				overflow: hidden;
				box-sizing: border-box;
				
				a {
					position: relative;
					margin-left: 2.2em;
					text-overflow: clip;
					white-space: nowrap;
					overflow: hidden;
					
					&:before {
						font-family: 'FontAwesome';
						content: "\f0c1";
						margin-right: .25em;
						position: absolute;
						left: -2.2em;
						
						text-align: center;
						color: white;
						background-color: $color__interactive;
						padding: .5em;
						line-height: 1;
						@include borde(50%);
					}
				}
			}
		}
		
	}
	
	/********** gallery **********/
	.gallery {
		width: auto;
		max-width: 100vw;
		margin: 0 -1em;
		padding: 0 0 1em;
		background-color: $color__sombreado;
		@include borde(0 0 1em 1em);
		
		@media screen and (min-width: $break__medium) {
			width: 49%;
			margin: 0;
		}
	}
	#photo-viewer {
		position: relative;
		height: 200px;
		overflow: hidden;
		background: $color__borde-recuadro;
		
		@media screen and (min-width: $break__small) {
			height: 300px;
		}
		img {
			position: absolute;
			max-width: 100%;
		    height: auto;
		    max-height: 100%;
			top: 50%;
			left: 50%;
		}
	}
	
	#photo-viewer.is-loading:after {
		content: url('img/load.gif');
		position: absolute;
		top: 50%;
		left: 50%;
	}
	

	
	
	/********** THUMBNAILS **********/
	#thumbnails {
		margin: 0;
		padding: 1em;
		height: auto;
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		overflow-x: auto;
		overflow-y: auto;
		
		//border-bottom: 1px solid $color__borde-recuadro;
		a {
			height: 80px;
			width: 80px;
			margin: 2px .25em;
			border: 2px solid $color__borde-recuadro;
			flex-shrink: 0;
			@include borde(2px);
			
			&:hover {
				//border: none;
			}
			
			img {
				height: 100%;
				width: auto;
			} 	  
		}
	}
	
	a.active {
		opacity: .5;
		border-color: $color__interactive;
	}
	


}

